"use client";

import { useEffect, useState } from "react";
import Button from "@/components/ui/button/Button";
import Input from "@/components/form/input/InputField";
import Label from "@/components/form/Label";
import { toast } from "react-hot-toast";

/* =========================
   Types
========================= */
export interface CustomerFormData {
  name: string;
  phone: string;
  email: string;
  address: string;
  customerType: string;
}

interface Props {
  open: boolean;
  mode: "add" | "edit";
  initialData?: CustomerFormData;
  onClose: () => void;
  onSubmit: (data: CustomerFormData) => void;
}

const emptyForm: CustomerFormData = {
  name: "",
  phone: "",
  email: "",
  address: "",
  customerType: "",
};

export default function CustomerFormModal({
  open,
  mode,
  initialData,
  onClose,
  onSubmit,
}: Props) {
  const [form, setForm] = useState<CustomerFormData>(emptyForm);
  const [errors, setErrors] =
    useState<Partial<Record<keyof CustomerFormData, string>>>({});
  const [loading, setLoading] = useState(false);

  /* =========================
     INIT FORM
  ========================= */
  useEffect(() => {
    setForm(mode === "edit" && initialData ? initialData : emptyForm);
    setErrors({});
  }, [mode, initialData, open]);

  /* =========================
     ESC TO CLOSE
  ========================= */
  useEffect(() => {
    const esc = (e: KeyboardEvent) => e.key === "Escape" && onClose();
    document.addEventListener("keydown", esc);
    return () => document.removeEventListener("keydown", esc);
  }, [onClose]);

  /* =========================
     UPDATE FIELD
  ========================= */
  const update = <K extends keyof CustomerFormData>(
    key: K,
    value: CustomerFormData[K]
  ) => {
    setForm((prev) => ({ ...prev, [key]: value }));
    setErrors((prev) => ({ ...prev, [key]: undefined }));
  };

  /* =========================
     VALIDATION
  ========================= */
  const validate = () => {
    const e: typeof errors = {};

    if (!form.name.trim()) {
      e.name = "Name is required";
      toast.error(e.name);
    }

    if (!form.phone.trim()) {
      e.phone = "Phone number is required";
      toast.error(e.phone);
    }

    if (!form.customerType.trim()) {
      e.customerType = "Customer type is required";
      toast.error(e.customerType);
    }

    if (form.email && !/^\S+@\S+\.\S+$/.test(form.email)) {
      e.email = "Invalid email address";
      toast.error(e.email);
    }

    setErrors(e);
    return Object.keys(e).length === 0;
  };

  /* =========================
     SUBMIT
  ========================= */
  const submit = async () => {
    if (!validate() || loading) return;
    setLoading(true);
    await onSubmit(form);
    setLoading(false);
  };

  if (!open) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50">
      <div
        className="w-full max-w-3xl bg-white dark:bg-gray-900 rounded-xl shadow-xl"
        onClick={(e) => e.stopPropagation()}
      >
        {/* Header */}
        <div className="relative p-5 border-b dark:border-gray-800">
          <h3 className="text-lg font-semibold text-center">
            {mode === "add" ? "Add Customer" : "Edit Customer"}
          </h3>
          <button
            onClick={onClose}
            className="absolute right-5 top-1/2 -translate-y-1/2 text-2xl"
          >
            √ó
          </button>
        </div>

        {/* Body */}
        <div className="p-6 grid grid-cols-1 md:grid-cols-2 gap-6">
          <Field label="Customer Name" required error={errors.name}>
            <Input
              value={form.name}
              onChange={(e) => update("name", e.target.value)}
            />
          </Field>

          <Field label="Phone" required error={errors.phone}>
            <Input
              value={form.phone}
              onChange={(e) => update("phone", e.target.value)}
            />
          </Field>

          <Field label="Email" error={errors.email}>
            <Input
              type="email"
              value={form.email}
              onChange={(e) => update("email", e.target.value)}
            />
          </Field>

          <Field label="Customer Type" required error={errors.customerType}>
            <select
              className="w-full rounded-md border px-3 py-2 dark:bg-gray-800"
              value={form.customerType}
              onChange={(e) => update("customerType", e.target.value)}
            >
              <option value="">Select type</option>
              <option value="REGULAR">Regular</option>
              <option value="VIP">VIP</option>
              <option value="WHOLESALE">Wholesale</option>
              <option value="CREDIT">Credit</option>
            </select>
          </Field>

          <Field label="Address" full>
            <Input
              value={form.address}
              onChange={(e) => update("address", e.target.value)}
            />
          </Field>
        </div>

        {/* Footer */}
        <div className="p-5 border-t dark:border-gray-800 flex justify-end gap-3">
          <Button variant="outline" onClick={onClose}>
            Cancel
          </Button>
          <Button onClick={submit} disabled={loading}>
            {loading ? "Saving..." : mode === "add" ? "Create" : "Update"}
          </Button>
        </div>
      </div>
    </div>
  );
}

/* =========================
   Field Helper
========================= */
function Field({
  label,
  children,
  error,
  required,
  full,
}: {
  label: string;
  children: React.ReactNode;
  error?: string;
  required?: boolean;
  full?: boolean;
}) {
  return (
    <div className={full ? "md:col-span-2" : ""}>
      <Label>
        {label} {required && <span className="text-red-500">*</span>}
      </Label>
      {children}
      {error && <p className="text-sm text-red-600 mt-1">{error}</p>}
    </div>
  );
}
