"use client";

import React, { useEffect, useRef, useState } from "react";
import Button from "@/components/ui/button/Button";
import Input from "@/components/form/input/InputField";
import CustomerFormModal, {
  CustomerFormData,
} from "./CustomerFormModal";
import ConfirmDeleteModal from "../Company/ConfirmDeleteModal";
import { SalesService } from "@/services/sales.service";
import toast from "react-hot-toast";

/* =========================
   Types
========================= */
interface CustomerDto {
  id: string;
  name: string;
  phone: string;
  email?: string;
  address?: string;
  customerType: string;
  createdAt?: string;
}

/* =========================
   Action Menu
========================= */
function ActionMenu({
  openUp,
  onEdit,
  onDelete,
}: {
  openUp?: boolean;
  onEdit: () => void;
  onDelete: () => void;
}) {
  const [open, setOpen] = useState(false);
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    function close(e: MouseEvent) {
      if (ref.current && !ref.current.contains(e.target as Node)) {
        setOpen(false);
      }
    }
    if (open) document.addEventListener("mousedown", close);
    return () => document.removeEventListener("mousedown", close);
  }, [open]);

  return (
    <div ref={ref} className="relative">
      <button
        onClick={() => setOpen((p) => !p)}
        className="rounded-full p-2 hover:bg-gray-100 dark:hover:bg-gray-700"
      >
        ‚ãÆ
      </button>

      {open && (
        <div
          className={`absolute right-0 z-50 w-36 rounded-lg border bg-white shadow-lg
          dark:border-gray-700 dark:bg-gray-900
          ${openUp ? "bottom-full mb-2" : "top-full mt-2"}`}
        >
          <button
            onClick={() => {
              onEdit();
              setOpen(false);
            }}
            className="flex w-full gap-2 px-4 py-2 text-sm hover:bg-gray-50 dark:hover:bg-gray-800"
          >
            ‚úèÔ∏è Edit
          </button>

          <button
            onClick={() => {
              onDelete();
              setOpen(false);
            }}
            className="flex w-full gap-2 px-4 py-2 text-sm text-red-600 hover:bg-red-50 dark:hover:bg-red-900/20"
          >
            üóë Delete
          </button>
        </div>
      )}
    </div>
  );
}

/* =========================
   Customer Table
========================= */
export default function CustomerTable() {
  const [customers, setCustomers] = useState<CustomerDto[]>([]);
  const [search, setSearch] = useState("");

  const [openModal, setOpenModal] = useState(false);
  const [mode, setMode] = useState<"add" | "edit">("add");
  const [selectedCustomer, setSelectedCustomer] =
    useState<CustomerDto | null>(null);

  const [openDelete, setOpenDelete] = useState(false);
  const [deleting, setDeleting] = useState(false);
  const [loading, setLoading] = useState(false);

  /* =========================
     LOAD CUSTOMERS
  ========================= */
  async function loadCustomers() {
    setLoading(true);
    try {
      const res = await SalesService.getAllCustomers();
      setCustomers(res.data.data);
    } catch {
      toast.error("Failed to load customers");
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => {
    loadCustomers();
  }, []);

  /* =========================
     ADD / UPDATE
  ========================= */
  async function handleSubmit(data: CustomerFormData) {
    try {
      if (mode === "add") {
        await SalesService.CreateCustomerAsyn(data);
        toast.success("Customer created successfully");
      } else {
        if (!selectedCustomer?.id) return;
        await SalesService.updateCustomer(selectedCustomer.id, data);
        toast.success("Customer updated successfully");
      }

      setOpenModal(false);
      loadCustomers();
    } catch {
      toast.error("Operation failed");
    }
  }

  /* =========================
     DELETE
  ========================= */
  async function confirmDelete() {
    if (!selectedCustomer) return;

    try {
      setDeleting(true);
      await SalesService.deleteCustomer(selectedCustomer.id);
      toast.success("Customer deleted successfully");
      setOpenDelete(false);
      loadCustomers();
    } catch {
      toast.error("Failed to delete customer");
    } finally {
      setDeleting(false);
    }
  }

  /* =========================
     FILTER
  ========================= */
  const filteredCustomers = customers.filter(
    (c) =>
      c.name.toLowerCase().includes(search.toLowerCase()) ||
      c.phone.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="w-full rounded-xl bg-white px-6 pb-6 dark:bg-gray-900">
      <h2 className="mb-4 text-xl font-semibold text-gray-800 dark:text-white">
        Customers
      </h2>

      {/* Top Bar */}
      <div className="mb-4 flex items-center justify-between">
        <Input
          placeholder="Search customer..."
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="w-64"
        />
        <Button
          size="sm"
          onClick={() => {
            setMode("add");
            setSelectedCustomer(null);
            setOpenModal(true);
          }}
        >
          + Add Customer
        </Button>
      </div>

      {/* Table */}
      <div className="overflow-x-auto">
        <table className="w-full border-collapse">
          <thead className="bg-gray-50 dark:bg-gray-800">
            <tr className="text-left text-sm">
              <th className="px-3 py-3">Name</th>
              <th className="px-3">Phone</th>
              <th className="px-3">Type</th>
              <th className="px-3">Email</th>
              <th className="px-3">Address</th>
              <th className="px-3 text-right">Action</th>
            </tr>
          </thead>

          <tbody>
            {loading ? (
              <tr>
                <td colSpan={6} className="py-6 text-center">
                  Loading customers...
                </td>
              </tr>
            ) : (
              filteredCustomers.map((c, i) => (
                <tr
                  key={c.id}
                  className="border-t hover:bg-gray-50 dark:hover:bg-gray-800"
                >
                  <td className="px-3 py-2 text-sm">{c.name}</td>
                  <td className="px-3 py-2 text-sm">{c.phone}</td>
                  <td className="px-3 py-2 text-sm">{c.customerType}</td>
                  <td className="px-3 py-2 text-sm">{c.email}</td>
                  <td className="px-3 py-2 text-sm">{c.address}</td>

                  <td className="px-3 text-right">
                    <ActionMenu
                      openUp={i === filteredCustomers.length - 1}
                      onEdit={() => {
                        setMode("edit");
                        setSelectedCustomer(c);
                        setOpenModal(true);
                      }}
                      onDelete={() => {
                        setSelectedCustomer(c);
                        setOpenDelete(true);
                      }}
                    />
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>

      {/* MODAL */}
      <CustomerFormModal
        open={openModal}
        mode={mode}
        initialData={
          selectedCustomer
            ? {
                name: selectedCustomer.name,
                phone: selectedCustomer.phone,
                email: selectedCustomer.email ?? "",
                address: selectedCustomer.address ?? "",
                customerType: selectedCustomer.customerType,
              }
            : undefined
        }
        onClose={() => setOpenModal(false)}
        onSubmit={handleSubmit}
      />

      {/* DELETE */}
      <ConfirmDeleteModal
        open={openDelete}
        loading={deleting}
        onClose={() => setOpenDelete(false)}
        onConfirm={confirmDelete}
      />
    </div>
  );
}
